"use client";

import { useEffect, useState } from "react";
import { useCourse } from "@/hooks/use-course";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Search, Loader2, Filter } from "lucide-react";
import { courseService } from "@/lib/course";

export default function CoursesPage() {
  const { courses, totalCount, isLoading, searchCourses } = useCourse();
  const [searchQuery, setSearchQuery] = useState("");
  const [category, setCategory] = useState("");
  const [skillLevel, setSkillLevel] = useState("");
  const [instructor, setInstructor] = useState("");
  const [categories, setCategories] = useState<string[]>([]);
  const [instructors, setInstructors] = useState<string[]>([]);

  // Fetch initial courses and filters on mount
  useEffect(() => {
    // Load all courses with a reasonable limit
    searchCourses({ limit: 50 });

    // Load filter options
    loadFilterOptions();
  }, []);

  const loadFilterOptions = async () => {
    try {
      const [cats, insts] = await Promise.all([
        courseService.getCategories(),
        courseService.getInstructors(),
      ]);
      setCategories(cats);
      setInstructors(insts);
    } catch (error) {
      console.error("Failed to load filter options:", error);
    }
  };

  const handleSearch = () => {
    const params: any = {
      limit: 50,
      offset: 0,
    };

    if (searchQuery.trim()) {
      params.query = searchQuery.trim();
    }
    if (category) {
      params.category = category;
    }
    if (skillLevel) {
      params.skill_level = skillLevel;
    }
    if (instructor) {
      params.instructor = instructor;
    }

    searchCourses(params);
  };

  const handleClearFilters = () => {
    setSearchQuery("");
    setCategory("");
    setSkillLevel("");
    setInstructor("");
    searchCourses({ limit: 50 });
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === "Enter") {
      handleSearch();
    }
  };

  return (
    <div className="container mx-auto py-12 px-4">
      <div className="mb-8">
        <h1 className="text-4xl font-bold mb-4">Course Catalog</h1>
        <p className="text-muted-foreground">
          {totalCount > 0
            ? `Browse ${totalCount} available courses`
            : "Search for courses"}
        </p>
      </div>

      {/* Search and Filters */}
      <div className="mb-8 space-y-4">
        {/* Search Bar */}
        <div className="flex gap-4">
          <Input
            placeholder="Search courses by title, description, or tags..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            onKeyPress={handleKeyPress}
            className="flex-1"
          />
          <Button onClick={handleSearch} disabled={isLoading}>
            {isLoading ? (
              <Loader2 className="h-4 w-4 animate-spin" />
            ) : (
              <>
                <Search className="h-4 w-4 mr-2" />
                Search
              </>
            )}
          </Button>
        </div>

        {/* Filters */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          <Select value={category} onValueChange={setCategory}>
            <SelectTrigger>
              <SelectValue placeholder="All Categories" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="">All Categories</SelectItem>
              {categories.map((cat) => (
                <SelectItem key={cat} value={cat}>
                  {cat}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>

          <Select value={skillLevel} onValueChange={setSkillLevel}>
            <SelectTrigger>
              <SelectValue placeholder="All Levels" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="">All Levels</SelectItem>
              <SelectItem value="beginner">Beginner</SelectItem>
              <SelectItem value="intermediate">Intermediate</SelectItem>
              <SelectItem value="advanced">Advanced</SelectItem>
            </SelectContent>
          </Select>

          <Select value={instructor} onValueChange={setInstructor}>
            <SelectTrigger>
              <SelectValue placeholder="All Instructors" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="">All Instructors</SelectItem>
              {instructors.map((inst) => (
                <SelectItem key={inst} value={inst}>
                  {inst}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>

          <Button
            variant="outline"
            onClick={handleClearFilters}
            disabled={isLoading}
          >
            <Filter className="h-4 w-4 mr-2" />
            Clear Filters
          </Button>
        </div>
      </div>

      {/* Course List */}
      {isLoading && courses.length === 0 ? (
        <div className="flex justify-center items-center py-12">
          <Loader2 className="h-8 w-8 animate-spin text-primary" />
          <span className="ml-2 text-muted-foreground">Loading courses...</span>
        </div>
      ) : courses.length > 0 ? (
        <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-3">
          {courses.map((course) => (
            <Card key={course.id} className="hover:shadow-lg transition-shadow">
              <CardHeader>
                <CardTitle className="line-clamp-2">{course.title}</CardTitle>
                <CardDescription className="line-clamp-3">
                  {course.description}
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-2">
                  <div className="flex items-center gap-2 flex-wrap">
                    <Badge variant="secondary">{course.category}</Badge>
                    <Badge variant="outline" className="capitalize">
                      {course.skillLevel}
                    </Badge>
                  </div>
                  <p className="text-sm text-muted-foreground">
                    <strong>Instructor:</strong> {course.instructor}
                  </p>
                  <p className="text-sm text-muted-foreground">
                    <strong>Duration:</strong> {course.duration}
                  </p>
                  {course.tags && course.tags.length > 0 && (
                    <div className="flex flex-wrap gap-1 mt-2">
                      {course.tags.slice(0, 4).map((tag, idx) => (
                        <Badge key={idx} variant="outline" className="text-xs">
                          {tag}
                        </Badge>
                      ))}
                      {course.tags.length > 4 && (
                        <Badge variant="outline" className="text-xs">
                          +{course.tags.length - 4} more
                        </Badge>
                      )}
                    </div>
                  )}
                  {course.relevanceScore && (
                    <p className="text-xs text-muted-foreground mt-2">
                      Relevance: {(course.relevanceScore * 100).toFixed(0)}%
                    </p>
                  )}
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      ) : (
        <div className="text-center py-12">
          <p className="text-muted-foreground text-lg">
            No courses found matching your criteria.
          </p>
          <Button
            variant="outline"
            onClick={handleClearFilters}
            className="mt-4"
          >
            Clear Filters and Show All
          </Button>
        </div>
      )}

      {/* Pagination Info */}
      {courses.length > 0 && (
        <div className="mt-8 text-center text-sm text-muted-foreground">
          Showing {courses.length} of {totalCount} courses
        </div>
      )}
    </div>
  );
}
